<template>
  <div class="home">
<!--    <img alt="Vue logo" src="../assets/logo.png">-->
<!--    <HelloWorld msg="Welcome to Your Vue.js App"/>-->
    rick and morty
      <paginate
              v-model="currentPage"
              :page-count="pages"
              :page-range="3"
              :margin-pages="2"
              :click-headler="clickCallback"
              :prev-text="'Prev'"
              :next-text="'Next'"
              :conteiner-class="'pagination'"
              :page-class="'page-item'"
      />
      <div class="character-list">
          <character-block
                  v-for="character in characters"
                  :key="character.id"
                  :character="character"
          />
      </div>
      <paginate
        v-model="currentPage"
        :page-count="pages"
        :page-range="3"
        :margin-pages="2"
        :click-headler="clickCallback"
        :prev-text="'Prev'"
        :next-text="'Next'"
        :conteiner-class="'pagination'"
        :page-class="'page-item'"
      />
  </div>
</template>

<script>
// @ is an alias to /src
//import HelloWorld from '@/components/HelloWorld.vue'
import CharacterBlock from "../components/CharacterBlock";
import Paginate from 'vuejs-paginate'

export default {
    name: 'rick-and-morty-list',
    components: {
        CharacterBlock,
        Paginate
    },
    data() {
        return {
            currentPage: 1,
        }
    },
    computed: {
        characters() {
            console.log(this.$store.getters['getCharactersByPage'](this.currentPage), 'characters');
            return this.$store.getters['getCharactersByPage'](this.currentPage);
        },
        firstCharacter() {
            return this.$store.getters['getCharacterById']({id: 1, page: 1});
        },
        pages() {
            return this.$store.state.pages;
        }
    },
    watch: {
        currentPage: {
            handler(page) {
                this.$store.dispatch('fetchCharacters', page);
            },
            immediate: true
        },

    },
    methods: {
        clickCallback(pageNum) {
            console.log({pageNum})
        },

    }
  // components: {
  //   HelloWorld
  // }
}
</script>
